<template>
    <v-chip-group :value="value" @change="handleChange" class="px-3" column multiple>
        <v-chip v-for="item in items" :key="item" filter outlined>
            {{ item }}
        </v-chip>
    </v-chip-group>
</template>

<script>
export default {
    name: 'NavChips',
    props: {
        value: {
            type: Array,
            required: true
        },
        items: {
            type: Array,
            required: true
        }
    },
    methods: {
        handleChange(value) {
            this.$emit('input', value)
        }
    }
}
</script>
