<template>
    <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
            <v-btn
                text
                class="bth-toggle-menu white--text"
                v-bind="attrs"
                v-on="on"
            >
                {{ item.name }}
                <v-icon right> mdi-menu-down</v-icon>
            </v-btn>
        </template>
        <v-list
            v-for="(subItem, index) in item.items"
            :key="subItem"
            class="grey lighten-3"
        >
            <v-list-item
                :class="{
                    'v-item--active v-list-item--active': subItem === tableName
                }"
                @click="selectPath(index)"
            >
                {{ subItem }}
            </v-list-item>
        </v-list>
    </v-menu>
</template>

<script>
export default {
    name: 'Menu',
    props: {
        tableName: {
            type: String,
            required: true
        },
        item: {
            type: Object,
            required: true
        },
        selectPath: {
            type: Function,
            required: true
        }
    }
}
</script>

<style lang="scss" scoped>
.bth-toggle-menu {
    height: 100% !important;
}
