<template>
    <v-card :elevation="8" class="card">
        <v-row>
            <v-col sm="2" class="py-0 d-flex">
                <v-text-field
                    label="Search for PT number"
                    class="px-0 py-0 mx-0 my-0 ml-4 mt-2"
                />
            </v-col>
            <v-col class="py-0" sm="3" align-self="center">
                <h2 ref="tables">{{ activeTable }}</h2>
            </v-col>
            <v-col class="py-0">
                <nav-tabs :items="tabs" v-model="activeTable">
                    <template v-slot:additional>
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    @click="handleToggleScroll"
                                    v-bind="attrs"
                                    v-on="on"
                                    class="ml-auto mr-2"
                                >
                                    <v-icon v-if="isSyncScroll">
                                        mdi-power-on
                                    </v-icon>
                                    <v-icon v-else
                                        >mdi-arrow-vertical-lock
                                    </v-icon>
                                </v-btn>
                            </template>
                            <span>
                                {{
                                    isSyncScroll
                                        ? 'Disable scroll sync'
                                        : 'Enable scroll sync'
                                }}</span
                            >
                        </v-tooltip>
                    </template>
                </nav-tabs>
            </v-col>
        </v-row>
        <v-data-table
            ref="table"
            class="table"
            :headers="dataTable.headers"
            disable-pagination
            fixed-header
            :key="activeTable"
            multi-sort
            @dblclick:row="handleClick"
            :items="dataTable.items"
            hide-default-footer
        >
            <template v-slot:item.trikot="{ item }">
                <chip-status :value="item.trikot" />
            </template>
            <template v-slot:item.cover="{ item }">
                <chip-status :value="item.cover" />
            </template>
            <template v-slot:item.rollPacked="{ item }">
                <chip-status :value="item.rollPacked" />
            </template>
            <template v-slot:item.springOrOtherMaterial="{ item }">
                <chip-status :value="item.springOrOtherMaterial" />
            </template>
            <template v-slot:item.id="{ item }">
                <span class="d-flex align-center justify-center">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                class="mr-2"
                                small
                                v-bind="attrs"
                                v-on="on"
                                :to="{
                                    name: 'PtInfo',
                                    params: { id: item.id }
                                }"
                            >
                                <v-icon color="orange darken-4">
                                    mdi-open-in-new</v-icon
                                >
                            </v-btn>
                        </template>
                        <span>View all information</span>
                    </v-tooltip>
                    {{ item.id }}
                </span>
            </template>
        </v-data-table>
    </v-card>
</template>

<script>
import faker from 'faker'
import { mapState } from 'vuex'

import NavTabs from '../components/NavTabs'
import ChipStatus from '../components/ChipStatus'

import mutationTypes from '@/store/types'
import schemes from '@/utils/types/schemes'
import fake from '@/utils/fake'
import { TTableNavsMain } from '@/utils/types/tableNavs'
import eventEmitter, { types } from '@/utils/eventEmitter'

export default {
    name: 'CoreTable',
    components: {
        NavTabs,
        ChipStatus
    },
    data: () => ({
        tabs: TTableNavsMain,
        activeTable: ''
    }),
    watch: {
        scroll(val) {
            this.$refs.table.$el.querySelector(
                '.v-data-table__wrapper'
            ).scrollTop = val
        },
        activeTable() {
            this.$refs.table.$el
                .querySelector('.v-data-table__wrapper')
                .addEventListener('scroll', this.onScroll)
        }
    },
    computed: {
        ...mapState({
            scroll: state => state.options.scrollTable,
            isSyncScroll: state => state.options.isSyncScrollTable
        }),
        dataTable() {
            return {
                headers: schemes[this.activeTable],
                items: fake[this.activeTable]
            }
        }
    },
    mounted() {
        this.$refs.table.$el
            .querySelector('.v-data-table__wrapper')
            .addEventListener('scroll', this.onScroll)
    },
    updated() {
        this.$refs.table.$el
            .querySelector('.v-data-table__wrapper')
            .addEventListener('scroll', this.onScroll)
    },
    beforeDestroy() {
        console.log(2)
        //this.$refs.table.$el
        //    .querySelector('.v-data-table__wrapper')
        //    .removeEventListener('scroll', this.onScroll)
    },
    methods: {
        onScroll(e) {
            console.log(e,this.isSyncScroll)
            if (this.isSyncScroll)
                this.$store.commit(mutationTypes.SET_SCROLL_TABLE, {
                    value: e.target.scrollTop
                })
        },
        handleToggleScroll() {
            this.$store.commit(mutationTypes.SET_SYNC_SCROLL_TABLE, {
                isSync: !this.isSyncScroll
            })
        },
        handleClick(event, { item }) {
            eventEmitter.$emit(types.openQuickList, {
                cursor: {
                    x: event.pageX,
                    y: event.pageY
                },
                mattress: {
                    id: item.id,
                    size: {
                        height: faker.random.float(),
                        width: faker.random.float(),
                        length: faker.random.float()
                    }
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.card {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.card {
    ::v-deep .v-data-table {
        height: calc(100% - 60px);
    }
}

.table {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.table > th,
.table > td {
    text-align: center !important;
}
</style>
