import faker from 'faker'
import { TTableNames } from '@/utils/types/types'

const count = 40
const ids = new Array(count)
    .fill({})
    .map(() => `PT${faker.random.uuid().slice(0, 5)}`)

const fake = {
    [TTableNames.mattressCoreDesign]: new Array(count)
        .fill({})
        .map((_, index) => ({
            id: ids[index],
            weightKg: faker.random.float(),
            trikot: faker.random.boolean(),
            cover: faker.random.boolean(),
            rollPacked: faker.random.boolean(),
            springOrOtherMaterial: faker.random.boolean(),
            numberOfLayers: faker.random.number(),
            crossDirectionMaterial: faker.lorem.word(),
            crossDirectionDesign: faker.lorem.word()
        })),
    [TTableNames.mattressDesign]: new Array(count).fill({}).map((_, index) => ({
        id: ids[index],
        mattressCoreDesign: faker.lorem.word(),
        mattressCoverDesign: faker.lorem.word(),
        componentDesign: faker.lorem.word(),
        prototypeIteration: faker.lorem.word(),
        cutouts: faker.random.boolean()
    })),
    [TTableNames.generalMattressInformation]: new Array(count)
        .fill({})
        .map((_, index) => ({
            id: ids[index],
            productCategory: faker.lorem.word(),
            internalName: faker.lorem.word(),
            internalNumber: faker.random.number(),
            slNumber: faker.random.number(),
            orderDate: faker.date.past(),
            orderBy: faker.name.findName(),
            projectName: faker.commerce.productName(),
            projectId: faker.random.number()
        })),
    [TTableNames.productionData]: new Array(count).fill({}).map((_, index) => ({
        id: ids[index],
        orderedDate: faker.date.past(),
        orderedBy: faker.name.findName(),
        supplier: faker.name.findName(),
        productionDate: faker.date.past(),
        boxType: faker.commerce.productMaterial(),
        pallet: faker.commerce.productMaterial(),
        deliveryPlace: faker.address.streetAddress(),
        trikot: faker.random.boolean(),
        cover: faker.random.boolean(),
        rollPacked: faker.random.boolean()
    }))
}

export function generatorFake(schema) {
    return ids.map(id => {
        const result = { id }
        schema.forEach(item => {
            if (item.value === 'id') return null
            result[item.value] = faker.lorem.word()
        })

        return result
    })
}

export default fake
