            <v-list v-else dense>
                <v-list-item>
                    <v-list-item-content>Height:</v-list-item-content>
                    <v-list-item-content>
                        {{ (Math.random() * 100).toFixed(1) }} CM
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>Foam name:</v-list-item-content>
                    <v-list-item-content>
                        {{ (Math.random() * 100).toFixed(1) }}
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>Cutouts Design:</v-list-item-content>
                    <v-list-item-content>
