<script>
import hotkeys from 'hotkeys-js'
import eventEmitter, { types } from '@/utils/eventEmitter'

export default {
    name: 'QuickListModal',
    data: () => ({
        activeTab: 0,
        isOpen: false,
        cursor: {
            x: 0,
            y: 0
        },
        mattress: {
            size: {
                height: 0,
                width: 0,
                length: 0
            },
            id: ''
        }
    }),
    watch: {
        isOpen(val) {
            if (val) {
                console.log(1)
            } else {
                console.log(2)
            }
        }
    },
    beforeMount() {
        eventEmitter.$on(types.openQuickList, this.handleOpen)
        hotkeys('alt+q,alt+w,alt+1,alt+2,alt+3,alt+4', this.handleHotKeyToggle)
    },
    beforeDestroy() {
        eventEmitter.$off(types.openQuickList, this.handleOpen)
        hotkeys.unbind(
            'alt+q,alt+w,alt+1,alt+2,alt+3,alt+4',
            this.handleHotKeyToggle
        )
    },
    methods: {
        handleOpen({ cursor, mattress }) {
            this.isOpen = true
            this.cursor = cursor
            this.mattress = mattress
        },
        handleClickTrip(index) {
            const cities = this.cityArray.slice()
            cities[index] = {
                ...cities[index],
                isShow: !cities[index].isShow
            }

            this.cityArray = cities
        },
        handleHotKeyToggle(event, handler) {
            if (!this.isOpen) return
            event.preventDefault()

            switch (handler.key) {
                case 'alt+w': {
                    this.isOpen = false
                    break
                }
                case 'alt+q': {
                    this.activeTab = 0
                    break
                }
                case 'alt+1': {
                    this.activeTab = 1
                    break
                }
                case 'alt+2': {
                    this.activeTab = 2
                    break
                }
                case 'alt+3': {
                    this.activeTab = 3
                    break
                }
                case 'alt+4': {
                    this.activeTab = 4
                    break
                }
            }
        }
    }
}
</script>

