<template>
    <v-card :elevation="8" class="card">
        <nav-tabs :items="tabs" v-model="activeTable" />
        <nav-chips
            v-if="chipsName.length"
            v-model="chipNamesActive"
            :items="chipsName"
        />
        <v-data-table
            ref="table"
            class="table"
            :headers="columns"
            disable-pagination
            fixed-header
            :key="activeTable"
            multi-sort
            :items="dataTable.items"
            hide-default-footer
        >
            <template v-slot:item.id="{ item }">
                <span class="d-flex align-center justify-center">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                class="mr-2"
                                small
                                v-bind="attrs"
                                v-on="on"
                                :to="{
                                    name: 'PtInfo',
                                    params: { id: item.id }
                                }"
                            >
                                <v-icon color="orange darken-4">
                                    mdi-open-in-new</v-icon
                                >
                            </v-btn>
                        </template>
                        <span>View all information</span>
                    </v-tooltip>
                    {{ item.id }}
                </span>
            </template>
        </v-data-table>
    </v-card>
</template>

<script>
import { mapState } from 'vuex'

import NavTabs from '../components/NavTabs'
import NavChips from './components/NavChips'

import schemes from '@/utils/types/schemes'
import { generatorFake } from '@/utils/fake'
import { TTableNavsTestResultData } from '@/utils/types/tableNavs'
import mutationTypes from '@/store/types'

export default {
    name: 'TestResultData',
    components: {
        NavTabs,
        NavChips
    },
    data: () => ({
        chipNamesActive: [],
        tabs: TTableNavsTestResultData,
        activeTable: ''
    }),
    watch: {
        scroll(val) {
            this.$refs.table.$el.querySelector(
                '.v-data-table__wrapper'
            ).scrollTop = val
        },
        activeTable() {
            this.$nextTick(() => {
                this.chipNamesActive = []
                this.chipsName.forEach((_, i) => this.chipNamesActive.push(i))
            })
            this.$refs.table.$el
                .querySelector('.v-data-table__wrapper')
                .addEventListener('scroll', this.onScroll)
        }
    },
    computed: {
        ...mapState({
            scroll: state => state.options.scrollTable,
            isSyncScroll: state => state.options.isSyncScrollTable
        }),
        chipsName() {
            if (!this.dataTable.headers) return []
            return [
                ...new Set(
                    this.dataTable.headers
                        .map(item => item.tableName)
                        .filter(item => item)
                )
            ]
        },
        columns() {
            if (this.dataTable.headers) {
                const names = this.chipNamesActive.reduce((acc, curr) => {
                    acc[this.chipsName[curr]] = true
                    return acc
                }, {})

                if (!Object.values(names).length) return this.dataTable.headers

                return this.dataTable.headers.filter(item => {
                    if (item.value === 'id') return true
                    return names[item.tableName]
                })
            }
            return []
        },
        dataTable() {
            if (!this.activeTable) return { headers: [], items: [] }
            return {
                headers: schemes[this.activeTable],
                items: generatorFake(schemes[this.activeTable])
            }
        }
    },
    mounted() {
        this.$refs.table.$el
            .querySelector('.v-data-table__wrapper')
            .addEventListener('scroll', this.onScroll)
    },
    updated() {
        this.$refs.table.$el
            .querySelector('.v-data-table__wrapper')
            .addEventListener('scroll', this.onScroll)
    },
    beforeDestroy() {
        console.log(2)
        //this.$refs.table.$el
        //    .querySelector('.v-data-table__wrapper')
        //    .removeEventListener('scroll', this.onScroll)
    },
    methods: {
        onScroll(e) {
            if (this.isSyncScroll)
                this.$store.commit(mutationTypes.SET_SCROLL_TABLE, {
                    value: e.target.scrollTop
                })
        }
    }
}
</script>

<style lang="scss" scoped>
.card {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.card {
    ::v-deep .v-data-table {
        height: calc(100% - 50px);
    }
}

.table {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.table > th,
.table > td {
    text-align: center !important;
}
</style>
