import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    { path: '/pt', redirect: { name: 'Home' } },
    {
        path: '/pt/:id',
        name: 'PtInfo',
        component: () => import('../views/PtInfo')
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
