import types from '../types'

export default {
    state: () => ({
        scrollTable: 0,
        isSyncScrollTable: false
    }),
    mutations: {
        [types.SET_SCROLL_TABLE](state, { value }) {
            state.scrollTable = value
        },
        [types.SET_SYNC_SCROLL_TABLE](state, { isSync }) {
            state.isSyncScrollTable = isSync
        }
    },
    actions: {},
    getters: {}
}
