<template>
    <v-card class="nav-tabs deep-purple accent-4">
        <v-tabs
            :value="path[0]"
            slider-color="white"
            background-color="deep-purple accent-4"
            center-active
        >
            <v-tab
                v-for="(item, index) in items"
                :key="index"
                class="px-0 py-0 tab"
                :link="false"
            >
                <nav-menu
                    v-if="typeof item === 'object'"
                    :item="item"
                    :table-name="value"
                    :selectPath="indexMenu => selectPath([index, indexMenu])"
                />
                <span v-else class="tab-content white--text" @click="selectPath([index])">
                    {{ item }}</span
                >
            </v-tab>
        </v-tabs>

        <div class="ml-auto mr-2">
            <slot name="additional" />
        </div>
    </v-card>
</template>

<script>
import Menu from './Menu'

export default {
    name: 'NavTabs',
    components: {
        NavMenu: Menu
    },
    props: {
        value: {
            type: String,
            required: true
        },
        items: {
            type: Array,
            required: true
        }
    },
    data: () => ({
        path: []
    }),
    created() {
        console.log(1)
        if (typeof this.items[0] === 'string') this.selectPath([0])
        else this.selectPath([0, 0])
    },
    methods: {
        selectPath(path) {
            const name =
                path.length === 2
                    ? this.items[path[0]].items[path[1]]
                    : this.items[path[0]]
            this.path = path
            this.$emit('input', name)
        }
    }
}
</script>

<style scoped lang="scss">
.nav-tabs {
    border-top-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tab {
    align-content: stretch;
    max-width: unset;
}
.tab-content {
    padding-left: 10px;
    padding-right: 10px;
    white-space: nowrap;
}
</style>
